import image1 from '../images/svg/ava01.svg';
import image2 from '../images/svg/ava02.svg';
import image3 from '../images/svg/ava03.svg';
import image4 from '../images/svg/ava04.svg';
import image5 from '../images/svg/ava05.svg';
import image6 from '../images/svg/ava06.svg';
import image7 from '../images/svg/ava07.svg';
import image8 from '../images/svg/ava08.svg';
import image9 from '../images/svg/ava09.svg';
import image11 from '../images/svg/ava11.svg';
import image12 from '../images/svg/ava12.svg';
import image13 from '../images/svg/ava13.svg';

function randomPick(arr) {
  const selected = arr[Math.floor(Math.random() * arr.length)];

  return selected;
}

const images = [image2, image3, image4, image5, image6, image7, image8, image9, image11, image12, image13];

const txt = ['Hallo', 'Hi', '안녕하세요', 'Hello', 'Hola', 'Moin', 'Yoo'];

// function randomHover() {}

const test1 = document.getElementById('hover');

test1.addEventListener('mouseenter', () => {
  const myImage = document.getElementById('hover');
  const selImage = randomPick(images);
  myImage.src = selImage;
  const selText = randomPick(txt);

  document.getElementById('yoo').innerText = `${selText}!`;
});

const test2 = document.getElementById('hover');

test2.addEventListener('mouseleave', () => {
  const myImage = document.getElementById('hover');
  myImage.src = image1;
  const selText = randomPick(txt);

  document.getElementById('yoo').innerText = `YTK ${selText}!`;
});
